.App {
  text-align: center;
}
.Projects {
  text-align: center;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
}
.bm-item-list a {
  padding: 0.8em;
}
.bm-item-list a span {
  margin-left: 10px;
  font-weight: 700;
}

.bm-item:focus {
  outline: none;
}
.bm-item:hover {
  color: white;
}
/* Individual item */
.bm-item {
  display: inline-block;
  color: #d1d1d1;
  transition: color 0.2s;
  text-decoration: none;
  margin-bottom: 15px;
}

/* The rest copied directly from react-burger-menu docs */

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}
a span {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1.05em;
}
